.item-card {
  width: 8rem;
  height: 3rem;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #f1f2f6;
  border-radius: 11px;
  margin: 0.2rem;
  color: #313531;
  cursor: pointer;
  padding: 1%;
  padding-top: 2%;
  padding-bottom: 0%;
  opacity: 1;
  box-shadow: 0px 4px 7px #00000029;
}

.item-card-adverb {
  width: 8rem;
  height: 3rem;
  background-color: #f5d38b;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #f1f2f6;
  border-radius: 11px;
  margin: 0.2rem;
  color: #313531;
  cursor: pointer;
  padding: 1%;
  padding-top: 2%;
  padding-bottom: 0%;
  opacity: 1;
  color: black;
  box-shadow: 0px 4px 7px #00000029;
}

.table-striped {
  background: transparent linear-gradient(268deg, #fdf5e6 0%, #fdf5e6 100%) 0%
    0% no-repeat padding-box;
  background-color: #d7edff;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 2vh;
  padding: 15vh;
}


.table-striped th,
.table-striped td {
  margin-top: 5px;
  box-shadow: 0px 4px 7px #00000029;
  overflow: auto;
  border-right: 5px;
  padding-left: 1vh;
  text-align: center;
  padding: 1.5vh 5vh 1.5vh 5vh;
  cursor: pointer;
}

.table-striped th {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.table-striped .drop-target {
  border-width: thin;
  background-color: #ffffff;
  margin: 3px;
  border-style: solid #b8b8b8;
  display: inline-block;
  border-radius: 1vh;
  padding: 0.3vh;
  box-shadow: inset 0px 3px 6px #00000029;
}  

.table-striped .normal_row {
  border-right: 5px;
}

.table-striped .row-border {
  /* border-bottom: 1px solid black; */
  margin-bottom: 2px;
  /* border-color: #7f7e7c; */
}
